<template>
    <v-layout justify-center>
      <v-flex xs12 sm8 md6>
        <v-card>
          <v-toolbar dark color="primary">
            <v-toolbar-title>Login</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form v-model="loginForm.validation" ref="loginForm">
              <v-text-field prepend-icon="person" name="username" label="Username" type="text" v-model.trim="loginForm.username" v-validate="'required|min:2'" :error-messages="errors.collect('username')" required></v-text-field>
              <v-text-field prepend-icon="lock" name="password" label="Password" id="password" type="password" v-model="loginForm.password" v-validate="'required'" :error-messages="errors.collect('password')" required></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="login" :loading="loginForm.loading">Login</v-btn>
            <v-btn flat :to="{path: '/Register'}">Register</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
</template>

<script>
import axios from 'axios'
import store from '@/store'
import router from '@/router'
export default {
  components: {
  },
  data () {
    return {
      loginForm: {
        validation: true,
        username: '',
        password: '',
        loading: false
      },
      snackbar: {
        visible: false,
        text: ''
      }
    }
  },
  methods: {
    login: function () {
      this.$validator.validateAll().then((valid) => {
        if (valid) {
          this.loginForm.loading = true
          axios.post('login', {
            'username': this.loginForm.username,
            'password': this.loginForm.password
          })
            .then(res => {
              // console.log('login response', res)
              this.$toast.show(res.data.status, 'Login')
              if (res.data.status === 'Success') {
                store.commit('login', res.data.api_token)
                router.push({name: 'Dashboard'})
              }
              this.loginForm.loading = false
            })
            .catch(err => {
              console.log(err)
              this.$toast.show('Network Error', 'Login')
              this.loginForm.loading = false
            })
        } else {
          this.$toast.show('Enter Correct Data', 'Login')
          this.loginForm.loading = false
        }
      })
    }
  }
}
</script>



