import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import router from './router/index'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    apiHost: 'http://localhost:8000',
    app: {
      name: 'Oktain Inc.',
      logged: false,
      api_token: '',
      user: {
      }
    },
    snackbar: {
      msg: '',
      show: false
    }
  },
  mutations: {
    setTitle: (state, title) => {
      state.title = title
    },
    showSnackbar: (state, msg) => {
      state.snackbar.msg = msg
      state.snackbar.show = true
    },
    hideSnackbar: (state) => {
      state.snackbar.show = false
    },
    login: (state, token) => {
      state.app.logged = true
      state.app.api_token = token
      localStorage.setItem('api_token', token)
      axios.defaults.headers.common['api_token'] = token
      axios.get('login/check')
        .then(res => {
          // console.log('token header', axios.defaults.headers)
          // console.log('login/check', res)
          if (res.data.status === 'Success') {
            // console.log('store login', res.data.user)
            state.app.user = res.data.user
          } else {
            router.push({name: 'Logout'})
          }
        })
        .catch(err => {
          console.log(err)
          router.push({name: 'Logout'})
        })
    },
    logout: (state) => {
      state.app.logged = false
      state.app.api_token = ''
      state.app.user = {}
      localStorage.removeItem('api_token')
      axios.defaults.headers.common['api_token'] = ''
      router.push({name: 'Login'})
    }
  },
  getters: {
    getApi: (state) => (path) => {
      return state.apiHost + '/api/v1/' + path
    }
  }
})



