<template>
    <v-layout justify-center>
        <v-flex xs12>
            <v-card>
                <v-toolbar dark color="primary">
                  <v-toolbar-title>Projects</v-toolbar-title>
                  <v-spacer></v-spacer>
                  <v-text-field
                    append-icon="search"
                    label="Search"
                    single-line
                    hide-details
                    v-model="search"
                    dark
                  ></v-text-field>
                  <v-btn icon @click="getAll" :loading="loading">
                    <v-icon>refresh</v-icon>
                  </v-btn>
                </v-toolbar>
                <v-card-text>
                    <v-data-table
                      v-model="selected"
                      :headers="headers"
                      :items="items"
                      :search="search"
                      :pagination.sync="pagination"
                      item-key="id"
                      class="elevation-1"
                      :loading="loading"
                    >
                      <template slot="headers" slot-scope="props">
                        <tr>
                          <th
                            v-for="header in props.headers"
                            :key="header.text"
                            :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                            @click="changeSort(header.value)"
                          >
                            <v-icon small>arrow_upward</v-icon>
                            {{ header.text }}
                          </th>
                        </tr>
                      </template>
                      <template slot="items" slot-scope="props">
                        <tr @dblclick="$router.push({name: 'Project', params: {id: props.item.id}} )" :class="[props.item.finished === 1 ? 'green lighten-5' : 'red lighten-5']">
                          <td>{{ props.item.created_at }}</td>
                          <td>{{ props.item.customer }}</td>
                          <td>{{ props.item.contact }}</td>
                          <td class="text-xs-center">{{ props.item.userCount }}</td>
                          <td class="text-xs-center">{{ props.item.productCount }}</td>
                          <td class="text-xs-center">{{ props.item.serviceCount }}</td>
                          <td class="text-xs-right">{{ props.item.total }}</td>
                          <td class="text-xs-right">{{ props.item.paid }}</td>
                          <td class="text-xs-right">{{ props.item.balance }}</td>
                        </tr>
                      </template>
                    </v-data-table>
                </v-card-text>
                <v-card-actions>
                </v-card-actions>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
  import axios from 'axios'
  export default {
    data: () => ({
      loading: true,
      search: '',
      pagination: {
      },
      selected: [],
      headers: [
        {
          text: 'Date',
          align: 'left',
          value: 'created_at'
        },
        { text: 'Customer', value: 'customer' },
        { text: 'Contact', value: 'contact' },
        { text: 'Members', value: 'userCount' },
        { text: 'Products', value: 'productCount' },
        { text: 'Services', value: 'serviceCount' },
        { text: 'Total', value: 'total' },
        { text: 'Paid', value: 'paid' },
        { text: 'Balance', value: 'balance' }
      ],
      items: [
      ]
    }),
    created () {
      this.getAll()
    },
    methods: {
      /* toggleAll () {
        if (this.selected.length) this.selected = []
        else this.selected = this.items.slice()
      }, */
      changeSort (column) {
        if (this.pagination.sortBy === column) {
          this.pagination.descending = !this.pagination.descending
        } else {
          this.pagination.sortBy = column
          this.pagination.descending = false
        }
      },
      getAll () {
        this.loading = true
        axios.get('project')
          .then(res => {
            this.items = res.data.all
            this.loading = false
            this.$toast.show(res.data.status, 'Projects')
          })
          .catch(() => {
            this.$toast.show('Network Error', 'Projects')
            // this.loading = false
          })
      }
    }
  }
</script>



