<template>
  <v-container grid-list-lg>
    <v-dialog v-model="deleteConfirm" max-width="500px">
      <v-card>
        <v-card-title>
          Confirm Project Deletion
        </v-card-title>
        <v-card-text>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" @click.stop="deleteProject">Delete</v-btn>
          <v-btn flat @click.stop="deleteConfirm=false">Cancel</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-layout row wrap>
      <v-flex xs12>
        <v-card>
          <v-toolbar dark color="primary">
            <v-toolbar-title>Project</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn icon @click="deleteConfirm=true" :loading="loading">
              <v-icon>delete</v-icon>
            </v-btn>
            <v-btn icon @click="getProject" :loading="loading">
              <v-icon>refresh</v-icon>
            </v-btn>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-layout row wrap>
                <v-flex xs12 md6 lg4>
                  <v-text-field v-model="project.created_at" prepend-icon="alarm" name="created_at" label="Started Date"
                                type="text" readonly></v-text-field>
                </v-flex>
                <v-flex xs12 md6 lg4>
                  <v-text-field v-model.trim="project.customer" prepend-icon="person" name="customer" label="Customer Name"
                                type="text" v-validate="'required|min:2'" :error-messages="errors.collect('customer')"></v-text-field>
                </v-flex>
                <v-flex xs12 lg4>
                  <v-text-field v-model.trim="project.contact" prepend-icon="phone" name="contact" label="Contact"
                                type="text"></v-text-field>
                </v-flex>
                <v-flex xs12 md6 lg4>
                  <v-text-field v-model.number="project.total" prepend-icon="attach_money" name="total" label="Total" type="text"
                                readonly></v-text-field>
                </v-flex>
                <v-flex xs12 md6 lg4>
                  <v-text-field v-model.number="project.paid" prepend-icon="attach_money" name="paid" label="Paid"
                                type="text" v-validate="'required|decimal:2'" :error-messages="errors.collect('paid')"></v-text-field>
                </v-flex>
                <v-flex xs12 lg4>
                  <v-text-field v-model.number="project.total - project.paid" prepend-icon="attach_money" name="balance" label="Balance"
                                type="text" readonly></v-text-field>
                </v-flex>
                <v-flex>
                  <v-switch :label="project.finished?'Finished':'Not Finished'" v-model="project.finished"></v-switch>
                </v-flex>
                <v-flex xs12>
                  <v-list>
                    <v-subheader>Project Team</v-subheader>
                    <v-divider></v-divider>
                    <v-list-tile v-for="m in members" :key="m.id" @click="toggleTeamMember(m)">
                      <v-list-tile-action>
                        <v-icon v-if="isTeamMember(m)">
                          done
                        </v-icon>
                      </v-list-tile-action>
                      <v-list-tile-title :class="isTeamMember(m)?'':'grey--text'">
                        {{m.name}}
                      </v-list-tile-title>
                    </v-list-tile>
                    <v-divider></v-divider>
                  </v-list>
                </v-flex>
              </v-layout>
            </v-form>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click.prevent="editProject">Save</v-btn>
          </v-card-actions>
          <br>
          <v-toolbar dark color="primary">
            <v-toolbar-title>
              Products
            </v-toolbar-title>
          </v-toolbar>

          <v-card-text>
            <v-layout wrap row>
              <v-flex xs12 md6 lg4>
                <v-text-field v-model.number="product.description" prepend-icon="description" name="product_description" label="Product Description"
                              type="text"></v-text-field>
              </v-flex>
              <v-flex xs12 md6 lg2>
                <v-text-field v-model.number="product.warranty" name="product_warranty" label="Warranty (Months)"
                              type="text"></v-text-field>
              </v-flex>
              <v-flex xs12 md6 lg3>
                <v-text-field v-model.number="product.quantity" name="product_quantity" label="Quantity"
                              type="text"></v-text-field>
              </v-flex>
              <v-flex xs12 md6 lg3>
                <v-text-field v-model.number="product.unit_price" name="product_unit_price" label="Unit Price"
                              type="text"></v-text-field>
              </v-flex>
              <v-flex>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" @click.prevent="editProduct" :disabled="!product.id">Update</v-btn>
                  <v-btn color="primary" @click.prevent="newProduct" :disabled="!!product.id">Save</v-btn>
                  <v-btn @click.prevent="product={}" flat>New</v-btn>
                </v-card-actions>
              </v-flex>
              <v-flex xs12>
                <v-list two-line>
                  <transition-group name="slide-y-transition">
                    <v-list-tile v-for="p in project.products" :key="p.id" @click="">
                      <v-list-tile-action>
                        <v-icon>label</v-icon>
                      </v-list-tile-action>
                      <v-list-tile-content>
                        <v-list-tile-title>
                          {{p.description}}
                        </v-list-tile-title>
                        <v-list-tile-sub-title>
                          {{p.quantity}} X {{p.unit_price}}
                        </v-list-tile-sub-title>
                      </v-list-tile-content>
                      <v-list-tile-action>
                        {{p.quantity * p.unit_price}}
                      </v-list-tile-action>
                      <v-list-tile-action-text>
                        <v-menu top left lazy>
                          <v-btn icon slot="activator">
                            <v-icon>more_vert</v-icon>
                          </v-btn>
                          <v-list>
                            <v-list-tile @click.prevent="product = p">
                              <v-list-tile-action>
                                <v-icon>edit</v-icon>
                              </v-list-tile-action>
                              <v-list-tile-title>
                                Edit
                              </v-list-tile-title>
                            </v-list-tile>
                            <v-list-tile @click.prevent="deleteProduct(p)">
                              <v-list-tile-action>
                                <v-icon>delete</v-icon>
                              </v-list-tile-action>
                              <v-list-tile-title>
                                Delete
                              </v-list-tile-title>
                            </v-list-tile>
                          </v-list>
                        </v-menu>
                      </v-list-tile-action-text>
                    </v-list-tile>
                  </transition-group>
                  <v-divider></v-divider>
                  <v-list-tile>
                    <v-list-tile-action>
                      <v-icon>done</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                      <v-list-tile-title>
                        Total
                      </v-list-tile-title>
                      <v-list-tile-sub-title>
                        {{project.productItemCount}} Product(s)
                      </v-list-tile-sub-title>
                    </v-list-tile-content>
                    <v-list-tile-action>
                      {{project.productSum}}
                    </v-list-tile-action>
                  </v-list-tile>
                </v-list>
                </v-flex>
            </v-layout>
          </v-card-text>

          <v-toolbar dark color="primary">
            <v-toolbar-title>
              Services
            </v-toolbar-title>
          </v-toolbar>


          <v-card-text>
            <v-layout wrap row>
              <v-flex xs12 md6>
                <v-text-field v-model.number="service.description" prepend-icon="description" name="service_description" label="Service Description"
                              type="text"></v-text-field>
              </v-flex>
              <v-flex xs12 md6>
                <v-text-field v-model.number="service.amount" name="service_amount" label="Service Price"
                              type="text"></v-text-field>
              </v-flex>
              <v-flex>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" @click.prevent="editService" :disabled="!service.id">Update</v-btn>
                  <v-btn color="primary" @click.prevent="newService" :disabled="!!service.id">Save</v-btn>
                  <v-btn @click.prevent="service={}" flat>New</v-btn>
                </v-card-actions>
              </v-flex>
              <v-flex xs12>
                <v-list two-line>
                    <v-list-group v-for="s in project.services" :key="s.id">
                      <v-list-tile slot="activator">
                        <v-list-tile-action>
                          <v-icon>label</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                          <v-list-tile-title>
                            {{s.description}}
                          </v-list-tile-title>
                          <v-list-tile-sub-title>
                            {{s.taskCount}} Task(s)
                          </v-list-tile-sub-title>
                        </v-list-tile-content>
                        <v-list-tile-action>
                          {{s.amount}}
                        </v-list-tile-action>
                        <v-list-tile-action-text>
                          <v-menu top left lazy>
                            <v-btn icon slot="activator">
                              <v-icon>more_vert</v-icon>
                            </v-btn>
                            <v-list>
                              <v-list-tile @click.prevent="service = s">
                                <v-list-tile-action>
                                  <v-icon>edit</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-title>
                                  Edit
                                </v-list-tile-title>
                              </v-list-tile>
                              <v-list-tile @click.prevent="deleteService(s)">
                                <v-list-tile-action>
                                  <v-icon>delete</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-title>
                                  Delete
                                </v-list-tile-title>
                              </v-list-tile>
                            </v-list>
                          </v-menu>
                        </v-list-tile-action-text>
                      </v-list-tile>

                      <v-list-tile>
                        <v-list-tile-action>
                        </v-list-tile-action>
                        <v-list-tile-content>
                          <v-text-field v-model="task.description" label="Task Description" @keyup.enter="addTask(s)"></v-text-field>
                        </v-list-tile-content>
                        <v-list-tile-action>
                          <v-btn icon @click.prevent="addTask(s)">
                            <v-icon>add</v-icon>
                          </v-btn>
                        </v-list-tile-action>
                      </v-list-tile>
                      <v-list-tile v-for="t in s.tasks" :key="`task${t.id}`" @click="">
                        <v-list-tile-action>
                        </v-list-tile-action>
                        <v-list-tile-action>
                          <v-icon>label_outline</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                          <v-list-tile-title>
                            {{t.description}}
                          </v-list-tile-title>
                        </v-list-tile-content>
                        <v-list-tile-action>
                          <v-btn icon @click.prevent="deleteTask(t)">
                            <v-icon>delete</v-icon>
                          </v-btn>
                        </v-list-tile-action>
                      </v-list-tile>
                    </v-list-group>
                  <v-divider></v-divider><!--=======================================-->
                  <v-list-tile>
                    <v-list-tile-action>
                      <v-icon>done</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                      <v-list-tile-title>
                        Total
                      </v-list-tile-title>
                      <v-list-tile-sub-title>
                        {{project.serviceCount}} Service(s)
                      </v-list-tile-sub-title>
                    </v-list-tile-content>
                    <v-list-tile-action>
                      {{project.serviceSum}}
                    </v-list-tile-action>
                  </v-list-tile>
                </v-list>
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>

    </v-layout>
  </v-container>
</template>

<script>
  import axios from 'axios'

  export default {
    props: {
      id: null
    },
    data () {
      return {
        loading: false,
        deleteConfirm: false,
        project: {},
        service: {
          id: 0,
          description: '',
          amount: 0
        },
        product: {
          id: 0,
          description: '',
          quantity: 0,
          unit_price: 0,
          warranty: 0
        },
        task: {
          id: 0,
          description: ''
        },
        members: {}
      }
    },
    watch: {
      id () {
        this.getProject()
        this.getMembers()
      }
    },
    mounted () {
      this.getProject()
      this.getMembers()
    },
    methods: {
      getProject () {
        this.loading = true
        axios.get('project/' + this.id)
          .then(res => {
            // console.log('res', res)
            this.$toast.show(res.data.status, 'Project')
            if (res.data.status === 'Fail') {
              this.$router.push({name: 'Projects'})
            } else {
              this.project = res.data.project
            }
            this.loading = false
          })
          .catch(err => {
            console.log('err', err)
            this.$router.push({name: 'Projects'})
            this.$toast.show('Network Error', 'Project')
            this.loading = false
          })
      },
      deleteProject () {
        this.loading = true
        axios.delete('project/' + this.id)
          .then(res => {
            // console.log('res', res)
            this.$toast.show(res.data.status, 'Project Delete')
            this.$router.push({name: 'Projects'})
            this.loading = false
          })
          .catch(err => {
            console.log('err', err)
            this.$toast.show('Network Error', 'Project Delete')
            this.loading = false
          })
      },
      getMembers () {
        this.loading = true
        axios.get('members')
          .then(res => {
            // console.log('res', res)
            this.members = res.data
            this.loading = false
          })
          .catch(err => {
            console.log('err', err)
            this.$toast.show('Network Error', 'Project')
            this.loading = false
          })
      },
      editProject () {
        this.$validator.validateAll().then(valid => {
          if (valid) {
            this.loading = true
            axios.put('project/' + this.id, {
              customer: this.project.customer,
              contact: this.project.contact,
              paid: this.project.paid,
              finished: this.project.finished
            })
              .then(res => {
                // console.log('res', res)
                this.$toast.show(res.data.status, 'Project')
                if (res.data.status === 'Fail') {
                  this.$router.push({name: 'Projects'})
                } else {
                  this.project = res.data.project
                }
                this.loading = false
              })
              .catch(err => {
                console.log('err', err)
                this.$router.push({name: 'Projects'})
                this.$toast.show('Network Error', 'Project')
                this.loading = false
              })
          } else {
            this.$toast.show('Enter Correct Data', 'Project')
          }
        })
      },
      newProduct () {
        this.loading = true
        axios.post('product/' + this.id, {
          description: this.product.description,
          warranty: this.product.warranty,
          unit_price: this.product.unit_price,
          quantity: this.product.quantity
        })
          .then(res => {
            // console.log('res', res)
            this.$toast.show(res.data.status, 'Service Add')
            this.getProject()
            this.loading = false
          })
          .catch(err => {
            console.log('err', err)
            this.$toast.show('Network Error', 'Service Add')
            this.loading = false
          })
      },
      editProduct () {
        this.loading = true
        axios.put('product/' + this.product.id, {
          description: this.product.description,
          warranty: this.product.warranty,
          unit_price: this.product.unit_price,
          quantity: this.product.quantity
        })
          .then(res => {
            // console.log('res', res)
            this.$toast.show(res.data.status, 'Service Update')
            this.getProject()
            this.loading = false
          })
          .catch(err => {
            console.log('err', err)
            this.$toast.show('Network Error', 'Service Update')
            this.loading = false
          })
      },
      newService () {
        this.loading = true
        axios.post('service/' + this.id, {
          description: this.service.description,
          amount: this.service.amount
        })
          .then(res => {
            // console.log('res', res)
            this.$toast.show(res.data.status, 'Service Add')
            this.getProject()
            this.loading = false
          })
          .catch(err => {
            console.log('err', err)
            this.$toast.show('Network Error', 'Service Add')
            this.loading = false
          })
      },
      editService () {
        this.loading = true
        axios.put('service/' + this.service.id, {
          description: this.service.description,
          amount: this.service.amount
        })
          .then(res => {
            // console.log('res', res)
            this.$toast.show(res.data.status, 'Service Update')
            this.getProject()
            this.loading = false
          })
          .catch(err => {
            console.log('err', err)
            this.$toast.show('Network Error', 'Service Update')
            this.loading = false
          })
      },
      deleteProduct (product) {
        this.loading = true
        axios.delete('product/' + product.id)
          .then(res => {
            // console.log('res', res)
            this.$toast.show(res.data.status, 'Product Delete')
            this.getProject()
            this.loading = false
          })
          .catch(err => {
            console.log('err', err)
            this.$toast.show('Network Error', 'Product Delete')
            this.loading = false
          })
      },
      deleteService (service) {
        this.loading = true
        axios.delete('service/' + service.id)
          .then(res => {
            // console.log('res', res)
            this.$toast.show(res.data.status, 'Service Delete')
            this.getProject()
            this.loading = false
          })
          .catch(err => {
            console.log('err', err)
            this.$toast.show('Network Error', 'Service Delete')
            this.loading = false
          })
      },
      addTask (service) {
        this.loading = true
        axios.post('task/' + service.id, {
          description: this.task.description
        })
          .then(res => {
            // console.log('res', res)
            this.$toast.show(res.data.status, 'Task Add')
            this.getProject()
            this.loading = false
          })
          .catch(err => {
            console.log('err', err)
            this.$toast.show('Network Error', 'Task Add')
            this.loading = false
          })
        this.task = {}
      },
      deleteTask (task) {
        this.loading = true
        axios.delete('task/' + task.id)
          .then(res => {
            // console.log('res', res)
            this.$toast.show(res.data.status, 'Task Delete')
            this.getProject()
            this.loading = false
          })
          .catch(err => {
            console.log('err', err)
            this.$toast.show('Network Error', 'Task Delete')
            this.loading = false
          })
      },
      isTeamMember (member) {
        // console.log(this.project.members)
        // let m = {}
        for (let m in this.project.members) {
          // console.log(this.project.members[m])
          if (this.project.members[m].id === member.id) {
            return true
          }
        }
        return false
      },
      toggleTeamMember (member) {
        this.loading = true
        if (this.isTeamMember(member)) {
          axios.delete(`project/${this.id}/${member.id}`)
            .then(res => {
              // console.log('res', res)
              this.$toast.show(res.data.status, 'Team Member Delete')
              this.getProject()
              this.loading = false
            })
            .catch(err => {
              console.log('err', err)
              this.$toast.show('Network Error', 'Team Member Delete')
              this.loading = false
            })
        } else {
          axios.post(`project/${this.id}/${member.id}`)
            .then(res => {
              // console.log('res', res)
              this.$toast.show(res.data.status, 'Team Member Add')
              this.getProject()
              this.loading = false
            })
            .catch(err => {
              console.log('err', err)
              this.$toast.show('Network Error', 'Team Member Add')
              this.loading = false
            })
        }
      }
    }
  }
</script>



