import Vue from 'vue'
import Router from 'vue-router'
import Err404 from '@/components/errors/Err-404'
import Login from '@/components/Login'
import Register from '@/components/Register'
import Dashboard from '@/components/Dashboard'
import Projects from '@/components/Projects'
import Members from '@/components/Members'
import Project from '@/components/Project'
import store from '../store'
import axios from 'axios'

Vue.use(Router)

const routes = [
  {
    path: '*',
    name: 'Err404',
    component: Err404
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: Dashboard
  },
  {
    path: '/',
    redirect: {
      name: 'Dashboard'
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/logout',
    name: 'Logout',
    beforeEnter () {
      store.commit('logout')
    }
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  },
  {
    path: '/projects',
    name: 'Projects',
    component: Projects
  },
  {
    path: '/new-project',
    name: 'New Project',
    beforeEnter () {
      axios.post('project')
        .then(res => {
          router.push('/')
          router.push({
            name: 'Project',
            params: {
              id: res.data.project.id
            }
          })
        })
    }
  },
  {
    path: '/members',
    name: 'Members',
    component: Members
  },
  {
    path: '/projects/:id',
    name: 'Project',
    component: Project,
    props: true
  }
]
const router = new Router({routes})

router.beforeEach(function (to, from, next) {
  // console.log(!localStorage.getItem('api_token'))
  // console.log('from')
  // console.log(from)
  // console.log('to')
  // console.log(to)
  // next()
  if ((to.name === 'Login') || (to.name === 'Register')) {
    next()
  } else {
    // console.log(localStorage.getItem('api_token'))
    if (!localStorage.getItem('api_token')) {
      router.push({name: 'Login'})
    } else {
      next()
    }
  }
})

export default router



