<template>
  <v-layout justify-center>
    <v-flex xs12>
      <v-card>
        <v-toolbar dark color="primary">
          <v-toolbar-title>Members</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-text-field
            append-icon="search"
            label="Search"
            single-line
            hide-details
            v-model="search"
            dark
          ></v-text-field>
          <v-btn icon @click="getAll" :loading="loading">
            <v-icon>refresh</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <v-data-table
            v-model="selected"
            :headers="headers"
            :items="items"
            :search="search"
            :pagination.sync="pagination"
            item-key="id"
            class="elevation-1"
            :loading="loading"
          >
            <template slot="headers" slot-scope="props">
              <tr>
                <th
                  v-for="header in props.headers"
                  :key="header.text"
                  :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                  @click="changeSort(header.value)"
                >
                  <v-icon small>arrow_upward</v-icon>
                  {{ header.text }}
                </th>
              </tr>
            </template>
            <template slot="items" slot-scope="props">
              <tr>
                <td>{{ props.item.id }}</td>
                <td>{{ props.item.name }}</td>
                <td>{{ props.item.mobile }}</td>
                <td class="text-xs-right">{{ props.item.projectCount }}</td>
              </tr>
            </template>
          </v-data-table>
        </v-card-text>
        <v-card-actions>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
  import axios from 'axios'
  export default {
    data: () => ({
      loading: true,
      search: '',
      pagination: {
      },
      selected: [],
      headers: [
        {
          text: '#',
          align: 'left',
          value: 'id'
        },
        { text: 'Name', value: 'name' },
        { text: 'Mobile', value: 'mobile' },
        { text: 'Project Count', value: 'projectCount' }
      ],
      items: [
      ]
    }),
    created () {
      this.getAll()
    },
    methods: {
      /* toggleAll () {
        if (this.selected.length) this.selected = []
        else this.selected = this.items.slice()
      }, */
      changeSort (column) {
        if (this.pagination.sortBy === column) {
          this.pagination.descending = !this.pagination.descending
        } else {
          this.pagination.sortBy = column
          this.pagination.descending = false
        }
      },
      getAll () {
        this.loading = true
        axios.get('members')
          .then(res => {
            this.items = res.data
            this.loading = false
            this.$toast.show('Successfully Loaded', 'Members')
          })
          .catch(() => {
            this.$toast.show('Network Error', 'Members')
            // this.loading = false
          })
      }
    }
  }
</script>



