<template>
  <v-app id="inspire">
    <v-navigation-drawer fixed v-model="drawer" v-if="logged" app>
      <v-list dense>
        <v-subheader>Hi, {{ app.user.name }}</v-subheader>
        <template v-for="(item, index) in menu">
          <v-subheader v-if="item.type === 'header'" :key="index">{{ item.header }}</v-subheader>
          <v-divider v-if="item.type === 'divider'" :key="index"></v-divider>
          <v-list-tile v-if="item.type === 'item'" :to="{name: item.name}" :key="index">
            <v-list-tile-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>{{ item.name }}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </template>

      </v-list>
    </v-navigation-drawer>
    <v-toolbar color="indigo" dark fixed app>
      <v-toolbar-side-icon @click.stop="drawer = !drawer" v-if="logged"></v-toolbar-side-icon>
      <v-toolbar-title>{{ app.name }}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items v-if="!logged" class="hidden-xs-only">
        <v-btn flat :to="{name: 'Login'}">
          Login
          <v-icon right>lock_open</v-icon>
        </v-btn>
        <v-btn flat :to="{name: 'Register'}">
          Register
          <v-icon right>assignment</v-icon>
        </v-btn>
      </v-toolbar-items>
      <div v-if="!logged" class="hidden-sm-and-up">
        <v-btn icon :to="{name: 'Login'}">
          <v-icon>lock_open</v-icon>
        </v-btn>
        <v-btn icon :to="{name: 'Register'}">
          <v-icon>assignment</v-icon>
        </v-btn>
      </div>
    </v-toolbar>
    <v-content>
      <v-container fluid fill-height>
        <v-slide-y-transition mode="out-in" v-if="transitioned">
          <router-view></router-view>
        </v-slide-y-transition>
        <router-view v-if="!transitioned"></router-view>
      </v-container>
    </v-content>
    <v-footer color="indigo" app>
      <span class="white--text">&copy; Oktain Inc. 2018</span>
    </v-footer>
  </v-app>
</template>

<script>
  import store from '@/store'

  export default {
    components: {
    },
    data () {
      return {
        drawer: null,
        transitioned: true,
        menu: [
          {
            type: 'divider'
          },
          {
            type: 'item',
            name: 'Dashboard',
            route: 'Dashboard',
            icon: 'dashboard'
          },
          {
            type: 'item',
            name: 'Members',
            route: 'Register',
            icon: 'people'
          },
          {
            type: 'item',
            name: 'Projects',
            route: 'Projects',
            icon: 'folder'
          },
          {
            type: 'item',
            name: 'New Project',
            route: 'NewProjects',
            icon: 'create_new_folder'
          },
          {
            type: 'divider'
          },
          {
            type: 'item',
            name: 'Logout',
            route: 'Logout',
            icon: 'power_settings_new'
          }
        ]
      }
    },
    computed: {
      app () {
        return store.state.app
      },
      logged () {
        return store.state.app.logged
      }
    }
  }
</script>

<style>
  body {
    -webkit-user-select: none;
    -webkit-user-drag: none;
  }
</style>



