<template>
  <v-layout justify-center>
    <v-flex xs12>
      <v-card loading="true">
        <v-toolbar dark color="primary">
          <v-toolbar-title>Dashboard</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon @click="getAll" :loading="loading">
            <v-icon>refresh</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <v-container grid-list-lg>
            <v-layout>
              <v-slide-y-transition>
                <v-flex xs12 md6 v-if="members">
                  <v-card hover :to="{name: 'Members'}" ripple>
                    <v-toolbar dark color="orange">
                      <v-toolbar-title>Members</v-toolbar-title>
                    </v-toolbar>
                    <v-card-text class="text-xs-center">
                      <p>From all <p class="blue--text display-2">{{members.count}}</p> Member(s) <p class="blue--text display-2">{{members.bestMember.name}}</p> is the best with contributing to <p class="blue--text display-2">{{members.bestMember.projectCount}}</p> Projects !</p>
                    </v-card-text>
                  </v-card>
                </v-flex>
              </v-slide-y-transition>
              <v-slide-y-transition>
                <v-flex xs12 md6 v-if="projects">
                  <v-card hover :to="{name: 'Projects'}" ripple>
                    <v-toolbar dark color="green">
                      <v-toolbar-title>Projects</v-toolbar-title>
                    </v-toolbar>
                    <v-card-text class="text-xs-center">
                      <p>From all <p class="blue--text display-2">{{projects.count}}</p> Project(s) <p class="blue--text display-2">{{projects.pendingCount}}</p>{{projects.pendingCount ? ' is/are not Completed.' : ' is not completed.'}}<br>These are the projects that is not completed,
                      <br><br>
                      <p v-for="project in projects.pendingProjects">
                        <v-divider></v-divider>
                        <span class="blue--text">{{project.customer}}</span> {{project.created_at}}
                      </p>
                    </v-card-text>
                  </v-card>
                </v-flex>
              </v-slide-y-transition>
            </v-layout>
          </v-container>

        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary">Login</v-btn>
          <v-btn flat :to="{path: '/Register'}">Register</v-btn>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
  import axios from 'axios'
  export default {
    components: {},
    data () {
      return {
        loading: true,
        members: null,
        projects: null
      }
    },
    created () {
      this.getAll()
    },
    methods: {
      getAll () {
        this.loading = true
        axios.get('dashboard')
          .then(res => {
            this.members = res.data.members
            this.projects = res.data.projects
            console.log(this.projects)
            this.loading = false
            this.$toast.show('Successfully Loaded', 'Members')
          })
          .catch(() => {
            this.$toast.show('Network Error', 'Members')
            // this.loading = false
          })
      }
    }
  }
</script>



