<template>
    <v-layout justify-center>
      <v-flex xs12 sm8 md6>
        <v-card>
          <v-toolbar dark color="primary">
            <v-toolbar-title>Register</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form ref="form" v-model="form.validation">
              <v-text-field prepend-icon="person" name="fullname" label="Full Name" type="text" v-model.trim="form.name" v-validate="'required|min:2'" :error-messages="errors.collect('fullname')" required></v-text-field>
              <v-text-field prepend-icon="phone" name="mobile" label="Mobile Number" type="text" v-model.trim="form.mobile" v-validate="'required|min:2'" :error-messages="errors.collect('mobile')" required></v-text-field>
              <v-text-field prepend-icon="person" name="username" label="Username" type="text" v-model.trim="form.username" v-validate="'required|min:2'" :error-messages="errors.collect('username')" required></v-text-field>
              <v-text-field prepend-icon="lock" name="password" label="Password" id="password" type="password" v-model="form.password" v-validate="'required|min:4|confirmed:password-retype'" :error-messages="errors.collect('password')" required></v-text-field>
              <v-text-field prepend-icon="lock" name="password-retype" label="Retype Password" id="password-retype" type="password" v-model="form.passwordConfirm" :error-messages="errors.collect('password')" required></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="register" :loading="form.loading">Register</v-btn>
            <v-btn flat :to="{name: 'Home'}">Cancel</v-btn>
            <v-btn flat @click="$refs.form.reset()">Clear</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
</template>

<script>
  import store from '@/store'
  import router from '@/router'
  import axios from 'axios'
export default {
    components: {
    },
    data () {
      return {
        form: {
          validation: true,
          name: '',
          username: '',
          mobile: '',
          password: '',
          passwordConfirm: '',
          loading: false
        }
      }
    },
    methods: {
      register () {
        this.$validator.validateAll().then((valid) => {
          if (valid) {
            this.form.loading = true
            axios.post('register', {
              'name': this.form.name,
              'username': this.form.username,
              'mobile': this.form.mobile,
              'password': this.form.password
            })
              .then(res => {
                // console.log('login response', res)
                this.$toast.show(res.data.status, 'Register')
                if (res.data.status === 'Success') {
                  store.commit('login', res.data.api_token)
                  router.push({name: 'Dashboard'})
                }
              })
              .catch(err => {
                console.log(err)
                this.$toast.show('Network Error', 'Register')
              })
            this.form.loading = false
          } else {
            this.$toast.show('Enter Correct Data', 'Register')
          }
        })
      }
    }
}
</script>



